export const jsonpReturn = (data) => { //处理jsonpReturn格式对象
  if (data.status == 2) {
    Store.dispatch('LogOut').then(res => {
      router.push({
        name: 'login'
      })
    })
    return
  } else {
    let s = (data).replace('jsonpReturn', '')
    s = JSON.parse(s.substring(1, s.length - 2))
    return s
  }
}

export const checkRequest = (res) => {
  let data = jsonpReturn(res.data)
  let msg = data.msg
  let status = data.status
  if (status == 1) {
    return msg
  } else {
    return false
  }
}

export const checkEmpty = (object) => { //判断对象是否为空
  let n = 0;
  let length = 0;
  for (let i in object) {
    length++
    if (!object[i]) {
      n--
    } else {
      n++
    }
  }
  if (n == length) {
    return true
  } else {
    return false
  }
}

export const getSessionStorageVueX = () => { //获取sessionStorage中的数据
  return JSON.parse(sessionStorage.getItem('vuex'))
}

export const setUserInfo = (userinfo, cookieExpires) => { //token在Cookie中存储的天数，默认1天
  return Cookies.set(USER_INFO, userinfo, {
    expires: cookieExpires || null
  })
}

export const getUserInfo = () => {
  const token = JSON.parse(Cookies.get(USER_INFO) ? Cookies.get(USER_INFO) : null)
  if (token) return token
  else return false
}

export const removeUserInfo = () => { //移除cookie
  Cookies.remove(USER_INFO);
  sessionStorage.clear()
}

